import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { isEmpty, map } from 'lodash';
import { Card, Row, Col, Button, Table, message } from 'antd';
import { BtnExport } from 'components/common/button';
import { CartHeadAction } from 'components/common/index';
import {
  getStudentBonusEstimation,
  getNotedBonus,
  addBonusEstimation,
  deleteBonusEstimation,
  exportBonusEstimation
} from 'services/studentBonusEstimation';
import FormSearchStudent from './FormSearchStudent';
import tableConfig from './tableConfig';
import './style.scss';

class StudentBonusEstimationPage extends Component {

  state ={
    searchParams: {},
    searchResult: [],
    selectedAddItems: [],
    selectedRemoveItems: [],
    selectedRowKeysForAdd: [],
    selectedRowKeysForRemove: [],
    addedStudents: [],
    searchTableLoading: false,
    addedTableLoading: false,
    isAddBtnLoading: false,
    isDeleteBtnLoading: false,
    isExportBtnLoading: false
  }

  handleSearch = async params => {
    console.log(params);
    
    this.setState({ searchTableLoading: true, addedTableLoading: true })
    try {
      const searchedStudents = await getStudentBonusEstimation(params);
      const notedBonus = await getNotedBonus({ yearEduId: params.yearEduId, semesterId: params.semesterId });
      if(searchedStudents.data) {
        this.setState({ searchResult: searchedStudents.data.items, searchParams: params });
      }
      if(notedBonus.data) {
        this.setState({ addedStudents: notedBonus.data.items });
      }
    } catch (error) {
      message.error('Có lỗi xãy ra');
    }
    this.setState({ searchTableLoading: false, addedTableLoading: false })
  };

  handleSelectToAddStudent = (selectedRowKeys, selectedRows) => {
    this.setState({ selectedRowKeysForAdd: selectedRowKeys, selectedAddItems: selectedRows });
  }

  handleSelectToRemoveStudent = (selectedRowKeys, selectedRows) => {
    this.setState({ selectedRowKeysForRemove: selectedRowKeys, selectedRemoveItems: selectedRows });
  }

  handleAddStudent = async() => {
    const { searchParams: { yearEduId, semesterId },  selectedAddItems } = this.state;
    this.setState({ isAddBtnLoading: true });
    const students = map(selectedAddItems, item => ({
      studentId: item.id,
      classId: item.classId,
      gradeIn4: item.gradeIn4,
      gradeIn10: item.gradeIn10,
      trainingScore: item.trainingScore
    }))
    try {
      await addBonusEstimation({
        yearEduId,
        semesterId,
        students
      });
      this.setState({ selectedRowKeysForAdd: [], addedStudents: [], isAddBtnLoading: false });
      this.handleSearch(this.state.searchParams);
      message.success('Thêm thành công');
    } catch (error) {
      this.setState({ isAddBtnLoading: false });
      message.error('Có lỗi xãy ra');
    }
  }

  handleRemoveStudent = async() => {
    const { selectedRemoveItems, searchParams: { yearEduId, semesterId } } = this.state;
    try {
      this.setState({ addedTableLoading: true, isDeleteBtnLoading: true });
      const deletingIds = map(selectedRemoveItems, item => item.id);
      await deleteBonusEstimation({
        semesterId,
        yearEduId,
        students: deletingIds
      });
      this.setState({ selectedRowKeysForRemove: [], addedTableLoading: false, isDeleteBtnLoading: false })
      this.handleSearch(this.state.searchParams);
      message.success('Xóa thành công');
    } catch (error) {
      this.setState({ addedTableLoading: false, isDeleteBtnLoading: false })
      message.success('Có lỗi xãy ra');
    }
  }

  handleExport = async() => {
    const { searchParams } = this.state;
    try {
      this.setState({ isExportBtnLoading: true });
      await exportBonusEstimation(searchParams);
      message.success('Xuất dữ liệu thành công');
    } catch(error) {
      message.error('Có lỗi xãy ra');
    }
    this.setState({ isExportBtnLoading: false });
  }

  render() {
    const {
      searchResult,
      addedStudents,
      selectedRowKeysForAdd,
      selectedRowKeysForRemove,
      searchTableLoading,
      addedTableLoading,
      isAddBtnLoading,
      isDeleteBtnLoading,
      isExportBtnLoading
    } = this.state;
    const searchResultRowSelection = {
      selectedRowKeys: selectedRowKeysForAdd,
      onChange: this.handleSelectToAddStudent
    };
    const selectedRowSelection = {
      selectedRowKeys: selectedRowKeysForRemove,
      onChange: this.handleSelectToRemoveStudent
    };
    return (
      <Card title="Xét khen thưởng cá nhân" className="student-bonus-estimation">
        <Row>
          <CartHeadAction>
            <BtnExport
              disabled={isEmpty(addedStudents)}
              title="Xuất Excel"
              onClick={this.handleExport}
              loading={isExportBtnLoading}
            />
          </CartHeadAction>
        </Row>
        <Row>
          <Col span={24}>
            <FormSearchStudent handleSearch={this.handleSearch} />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table
              columns={tableConfig}
              dataSource={searchResult}
              rowSelection={searchResultRowSelection}
              loading={searchTableLoading}
            />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <div className="btn-group">
              <Button
                className="btnAction btnAdd"
                size="large"
                icon="plus-circle"
                onClick={this.handleAddStudent}
                disabled={isEmpty(selectedRowKeysForAdd)}
                loading={isAddBtnLoading}
              >
                Thêm SV
              </Button>
              <Button
                className="btnAction btnDelete"
                size="large"
                icon="delete"
                onClick={this.handleRemoveStudent}
                disabled={isEmpty(selectedRowKeysForRemove)}
                loading={isDeleteBtnLoading}
              >
                Xóa SV
              </Button>
            </div>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table
              dataSource={addedStudents}
              columns={tableConfig}
              rowSelection={selectedRowSelection}
              loading={addedTableLoading}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <StudentBonusEstimationPage />
  </Page>
);


